import React,{Component} from 'react';
import axios from 'axios';
import './css/Login.css';
import {config} from '../Constant';
import AuthContext from '../Context';

class LoginBox extends Component{
  constructor(props){
    super(props);
    this.url=config.url.API_URL+'/login'
    this.state={
      usrname : '',
      password:'',
      isSubmitting:false,
      isRemember:true,
      errmsg:null
    }
  }

  static contextType = AuthContext;

  handleInputChange = (event) => {
    const value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;
    const name = event.target.name;
    this.setState({
      [name]: value
    });
  }
  

  onSubmit = (event) => {
    event.preventDefault();
    this.setState({isSubmitting:true});
    axios.get(this.url,{
        auth: {
          username: this.state.usrname,
          password: this.state.password
        },
      }).then(data => {
          if(data.status===200){
            this.context.action({
              type:'LOGIN',
              payload:{
                isLocal: this.state.isRemember,
                token:data.data.token
              }
            });
          }
        })
        .catch(err=>{
          console.log("Error: ",err);
          this.setState({isSubmitting:false,errmsg:err.message||err.statusText});
          // if(err.message === "Network Error"){
          //   window.open(config.url.API_URL+"/about","_blank");
          // }

        });
  }

  render(){
    return(
      <div className="login-page">
        <div className="row">
          <div className="col-0 col-sm-0"></div>
          <div className="col-12 col-sm-5">
            <div className="login-box card">
          <div className="card-header">
            <h3>Sign In</h3>
			  	    <div className="d-flex justify-content-end social_icon">
			  		  <span><i className="fab fa-facebook-square"></i></span>
			  		  <span><i className="fab fa-google-plus-square"></i></span>
			  		  <span><i className="fab fa-twitter-square"></i></span>
			  	  </div>
            <div className="card-body">
			  	    <form onSubmit={this.onSubmit}>
			  		    <div className="input-group form-group">
			  			    <div className="input-group-prepend">
			  				  <span className="input-group-text"><i className="fas fa-user"></i></span>
			  			  </div>
                  <input  
                    type="text"
                    className="form-control"
                    name="usrname"
                    placeholder="Enter Username"
                    value={this.state.usrname}
                    onChange={this.handleInputChange}
                    required
                  />
					      </div>

					      <div className="input-group form-group">
					    	  <div className="input-group-prepend">
					    		  <span className="input-group-text"><i className="fas fa-key"></i></span>
					    	  </div>
                  <input
                    type="password"
                    name="password"
                    className="form-control"
                    placeholder="Enter Password"
                    value={this.state.password}
                    onChange={this.handleInputChange}
                    required
                  />
					      </div>
					      <div className="row align-items-center remember">
                  <input  type="checkbox"
                          defaultChecked="true"
                          name = "isRemember"
                          checked={this.isRemember}
                          onChange={this.handleInputChange}/>
                  <label className="form-check-label" htmlFor="exampleCheck1">Remember Me</label>
					      </div>
					      <div className="form-group">
						      <input type="submit" value="Login" className="btn float-right login_btn"/>
					      </div>
				    </form>
          </div>
          </div>
        </div>
          </div>
          <div className="col-0 col-sm-7"></div>
        </div>
        {this.state.errmsg && 
        <div className="row"> <p>Error: {this.state.errmsg}</p></div>
        }
      </div>
    );
  }
}

class Login extends Component {
  render(){
    return(
      <LoginBox/>
    );
  }
}

export default Login