import React,  {Component} from 'react';
import axios from 'axios';
import AuthContext from "../Context";
import {Redirect} from "react-router-dom";
import "./css/VocabularyCard.css"

const ContentCard = (props)=>{
  let kanji;
  if(props.state.vocab.kanji){
    kanji = <div><h2 className="kanji-font">{props.state.vocab.kanji}</h2>
            { props.state.isShow ?
              <p>&#123;{props.state.vocab.kana}&#125;</p>
              :
              <p>	&ensp;</p>
            }
            </div>

  }else{
    kanji = <div><h2 className="kanji-font">{props.state.vocab.kana}</h2>
              <p>	&ensp;</p>
            </div>
  }
  let cardbox = <div className="card-box card">
                  <div className="card-header">
                  <i class="material-icons">pages</i>
                    <span>{props.cardTitle}</span>
                  </div>
                  <div className="card-body">
                    {kanji}
                    <p>&ensp;</p>

                    { props.state.isShow ?
                      <p>English: {props.state.vocab.english}</p>
                      :
                      <p>	&ensp;</p>
                    }
                  </div>
                  <div className="card-footer">
                    <button type="button" 
                            className="btn float-right btn-primary d-flex" 
                            onClick={props.handleClick}>
                      <span>Next</span>
                      <i className="material-icons">chevron_right</i>
                    </button>
                    <button type="button" 
                          className="btn float-left btn-primary d-flex"
                          onClick={props.handleShow}>
                      {
                        props.state.isShow ?
                        <i className="material-icons">toggle_on</i>
                        :
                        <i className="material-icons">toggle_off</i>
                      }
                      <span>{ props.state.isShow ? "Hide":"Show"}</span>
                    </button>
                </div>
              </div>
  return(
    <div className="vocab-card container ">
    {props.state.errmsg ? 
      <Redirect to="/logout"/>
    :
    <div className="row">
      <div className="col-0 col-sm-0"></div>
      <div className="col-12 col-sm-6">
        {cardbox}
      </div>
      <div className="col-0 col-sm-6"></div>
    </div>
    }
    </div>
  );
}


class VocabularyCard extends Component{
  constructor(props){
    super(props);
    this.state = {
      vocab:[],
      isShow:false,
      errmsg:null
    }
  } 

  static contextType = AuthContext;

  handleClick = () => { 
    this.setState({isShow:false});
    this.getData(); 
  }

  // handleKeyPress = (event) => {
  //   console.log("LOG",event.charCode);
  //   this.handleClick();
  // }
  
  async getData(){
    await axios.get(this.props.apiUrl,{
      headers:{
        Accept:'application/json',
        Authorization:'Token '+ this.context.state.token
      },
    })
    .then( data => {
      this.setState({vocab:data.data.result});
    })
    .catch(err=>{
      console.log("error: ",err);
      if (err.response){
        this.setState({errmsg:err.response.status||err.response.data});
      }else if(err.request){
        this.setState({errmsg:err.request});
      }else{
        this.setState({errmsg:err.message});
      }
      return null;
    })
  }

  componentDidUpdate(prevProps){
    if(this.props.apiUrl !== prevProps.apiUrl)
      this.getData();
  }

  componentDidMount(){
    this.getData();
  }

  render(){
    // if (this.state.vocab.length === 0){
    //   TODO: Waiting animation or something
    // }

    return(
      <ContentCard 
        cardTitle={this.props.cardTitle}
        state={this.state} 
        handleClick={this.handleClick}
        handleShow={()=>{this.setState({isShow:!this.state.isShow})}}
      />
    );
  }

 

}

export default VocabularyCard;
