import React,{Component} from "react";
import {Redirect} from 'react-router-dom';
import "./css/Home.css";
import {AuthConsumer} from "../Context";

export default class Home extends Component{
  render(){
    return(
      <AuthConsumer>
       {(props) => {
          return props.state.isAuthenticate ?
            <div className="Home"> 
            <div className="lander">
              <h1>This is the empty main container</h1>
              <p>A simple main container with app token: {props.state.token}</p>
            </div>
            </div>
          : 
            <Redirect to="/login"/>
          }
        }
      </AuthConsumer>

    );
  }
}