export default class App extends Component{
  constructor(props){
    super(props);
    if(localStorage.getItem('token')){
      this.state = {
        isAuthenticate:true,
        isLocal:true,
        token:localStorage.getItem('token')
      };
    }else if(sessionStorage.getItem('token')){
      this.state = {
        isAuthenticate:true,
        isLocal:false,
        token:sessionStorage.getItem('token')
      };
    }    

  }

  
  state = {
    isAuthenticate: false,
    isLocal : true,
    token : null
  }

  stateAction = (action) => {
    switch(action.type){
      case "LOGIN" :
        action.payload.isLocal ?
          localStorage.setItem("token",action.payload.token)
          :
          sessionStorage.setItem("token",action.payload.token);
        this.setState({
            isAuthenticate:true,
            isLocal:action.payload.isLocal,
            token:action.payload.token
          });
      break;

      case "LOGOUT":
        this.state.isLocal ? 
          localStorage.clear()
          :
          sessionStorage.clear();
        this.setState({
          isAuthenticate:false,
          isLocal:true,
          token:null
        });
      break;

      default:
        return null;
    }
  }

  render() {
    return (
      <AuthProvider
        value={{
          state: this.state,
          action: this.stateAction
        }}
      >
      <Router basename={config.url.BASENAME}>
        <div className="App container">
          <NavBar state={this.state}/>
          <SwitchRoutes/>
        </div>
      </Router>
      </AuthProvider>
    );
  }



}


